import React, { useState, useEffect } from "react";
import styled from "styled-components";

const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  pointer-events: ${props => (props.disabled ? "none" : "")};
  color: ${props => (props.disabled ? "gray" : "")};
  flex: 1;
  margin-top: 40px;
  background-color: #f2f2f2;
  padding: 20px;
  justify-content: space-around;
  cursor: pointer;
`;

const Title = styled.div`
  font-size: 16px;
`;

const Text = styled.div`
  font-weight: bold;
  font-size: 20px;
  margin-bottom: 20px;
`;

const RecommendButton = ({ text, problem, onToProblem }) => {
  console.log("pppppp__________________", problem);
  return (
    <Container disabled={!problem} onClick={() => onToProblem(problem.id)}>
      <Text>{text}</Text>
      <Title>{problem ? problem.title : "没有啦！"}</Title>
    </Container>
  );
};

export default RecommendButton;
