import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { getDataFromUrl } from "../../../utils/urlData";

const Container = styled.div`
  margin-top: 20px;
  text-align: left;
  img {
    object-fit: contain;
    max-width: 100%;
  }
`;
const Content = styled.div`
  margin-top: 20px;
  font-size: 14px;
  color: #262626;
`;
const Description = ({ descriptionUrl }) => {
  const [description, setDescription] = useState("");
  useEffect(() => {}, [
    getDataFromUrl(descriptionUrl, "data.content", str => {
      setDescription(str);
    })
  ]);
  return (
    <Container>
      <Content dangerouslySetInnerHTML={{ __html: description }} />
    </Container>
  );
};

export default Description;
