import React from "react";
import { useMutation } from "@apollo/react-hooks";
import Loading from "../common/Loading";
import ModalHint from "../common/ModalHint";
import styled from "styled-components";

const Container = styled.div`
  display: flex;
  align-items: center;
`;

const ClickWrapper = ({
  variables,
  children,
  gql,
  autoLoading,
  onCompleted,
  onError
}) => {
  const [mutate, { data, loading }] = useMutation(gql, {
    variables,
    onCompleted: onCompleted,
    onError: onError
      ? onError
      : error => {
          ModalHint.error(error.toString());
        }
  });
  const content = (
    <Container onClick={() => mutate({ variables })}>{children}</Container>
  );
  if (autoLoading && loading) {
    return <Loading>{content}</Loading>;
  }
  return content;
};

export default ClickWrapper;
