import React, { useState, useEffect } from "react";
import styled from "styled-components";
import InfoItem from "../../../common/segaments/InfoItem";
import { SWITCH_FOLLOW, SWITCH_UP } from "../../../common/mutationGql";
import { OBJ_TYPES } from "../../../common/CONSULT";
import ClickWrapper from "../../ClickWrapper";
import Space from "../../../common/Space";
import Author from "../../blog/BlogSegaments/blogContent/Author";
import { getOptionByKey } from "../../../utils/optionsUtils";
import { PROBLEM_STATUS } from "../../../common/OPTIONS";

const Container = styled.div`
  width: 100%;
  display: flex;
  align-items: center;
  background-color: white;
  padding: 5px 20px;
`;

const BasicInfo = ({ problem, refetchProblem }) => {
  const {
    id,
    creator,
    lastUpdated,
    upCount,
    currentUpped,
    followerCount,
    currentFollowed,
    status,
    readCount
  } = problem;
  const statusOption = getOptionByKey(PROBLEM_STATUS, "value", status);
  return (
    <Container>
      <Author creator={creator} />
      <Space width={20} />
      <InfoItem
        hintText={"最近更新日期"}
        icon={"calendar alternate outline"}
        text={lastUpdated}
      />
      <Space width={20} />
      <ClickWrapper
        gql={SWITCH_UP}
        variables={{ objId: id, objType: OBJ_TYPES.PROBLEM }}
        onCompleted={() => refetchProblem()}
      >
        <InfoItem
          iconStyle={{ color: currentUpped ? "#F76976" : "" }}
          hintText={currentUpped ? "取消喜欢" : "点击喜欢"}
          icon={currentUpped ? "heart" : "heart outline"}
          text={upCount}
        />
      </ClickWrapper>
      <Space width={20} />
      <ClickWrapper
        gql={SWITCH_FOLLOW}
        variables={{ objId: id, objType: OBJ_TYPES.PROBLEM }}
        onCompleted={() => refetchProblem()}
      >
        <InfoItem
          iconStyle={{ color: currentFollowed ? "#F76976" : "" }}
          hintText={currentFollowed ? "取消收藏" : "点击收藏"}
          icon={currentFollowed ? "star" : "star outline"}
          text={followerCount}
        />
      </ClickWrapper>
      <InfoItem
        hintText={"当前状态"}
        icon={"bullseye"}
        boxStyle={{ color: statusOption.bColor }}
        text={statusOption.label}
      />
      <Space width={20} />
      <InfoItem
        hintText={"浏览次数"}
        icon={"eye"}
        text={Number(readCount) + 1}
      />
    </Container>
  );
};

export default BasicInfo;
