import React, { useEffect, useState } from "react";
import styled from "styled-components";
import BasicInfo from "./components/BasicInfo";
import { getRouteId } from "../../utils/routeUtils";
import { GET_PROBLEM, GET_PROGRAM } from "../../common/queryGql";
import PageItem from "../QueryItem";
import Description from "./components/Description";
import RecommendButton from "./components/RecommendButton";
import PersistStorage from "../../utils/PersistStorage";
import {
  OBJ_TYPES,
  PROBLEM_PAGE,
  SEARCHED_PROBLEMS
} from "../../common/CONSULT";
import _findIndex from "lodash/findIndex";
import _get from "lodash/get";
import Space from "../../common/Space";
import TypeTag from "./components/TypeTag";
import { useRead } from "../../common/hooks";
import PageTitle from "../../common/PageTitle";

const Container = styled.div`
  width: 1300px;
  padding: 30px 100px;
`;

const ArticleBox = styled.div`
  background-color: #f2f2f2;
  padding: 30px;
  width: 100%;
  min-height: 600px;
  position: relative;
`;

const Header = styled.div`
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 30px;
`;

const ActionsBox = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
`;

const useProblem = ({ problem, onGoTo }) => {
  useRead({ objId: problem.id, objType: OBJ_TYPES.PROBLEM });
  const [preProblem, setPreProblem] = useState(null);
  const [nextProblem, setNextProblem] = useState(null);

  useEffect(() => {
    const searchProblems = PersistStorage.getItem(SEARCHED_PROBLEMS);
    if (searchProblems instanceof Array) {
      const index = _findIndex(searchProblems, p => p.id === problem.id);
      if (index >= 0) {
        const preProblem = _get(searchProblems, `[${index - 1}]`, null);
        const nextProblem = _get(searchProblems, `[${index + 1}]`, null);
        setPreProblem(preProblem);
        setNextProblem(nextProblem);
      }
    }
  }, []);
  return {
    preProblem,
    nextProblem,
    onToProblem: id => onGoTo(`${PROBLEM_PAGE}${id}`)
  };
};

const _Problem = ({ refetchProblem, problem, onGoTo }) => {
  const { preProblem, nextProblem, onToProblem } = useProblem({
    problem,
    onGoTo
  });
  const { title, creator, descriptionUrl, type } = problem;
  return (
    <Container>
      <PageTitle title={`${title}`} metaKeywords={`${title}`} />
      <ArticleBox>
        <TypeTag type={type} />
        <Header>{title}</Header>
        <BasicInfo problem={problem} refetchProblem={refetchProblem} />
        <Description descriptionUrl={descriptionUrl} />
      </ArticleBox>
      <ActionsBox>
        <RecommendButton
          onToProblem={onToProblem}
          problem={preProblem}
          text={"上一条"}
        />
        <Space width={"30px"} />
        <RecommendButton
          onToProblem={onToProblem}
          problem={nextProblem}
          text={"下一条"}
        />
      </ActionsBox>
    </Container>
  );
};

const Problem = ({ onGoTo, history, location, currentUser }) => {
  return (
    <PageItem
      autoLoading
      id={getRouteId()}
      itemPath={"problem"}
      gql={GET_PROBLEM}
    >
      {({ loading, refetch, item }) => {
        return (
          <_Problem onGoTo={onGoTo} problem={item} refetchProblem={refetch} />
        );
      }}
    </PageItem>
  );
};

export default Problem;
